html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 18px;
}

body {
  background-color: #55afca;
}

.header {
  height: 143.7vw;
}
@media (min-width: 360px) {
  .header {
    background-image: url("/build/img/mobile/image-header.jpg");
    background-position: top;
    background-repeat: no-repeat;
    background-size: contain;
  }
}
@media (min-width: 768px) {
  .header {
    background-image: url("/build/img/desktop/image-header.jpg");
    background-position: bottom;
    background-size: cover;
    height: 100vh;
  }
}
.header p {
  color: white;
  font-family: "Fraunces", serif;
  font-size: 5rem;
  font-weight: 900;
  letter-spacing: 0.8rem;
  margin-top: 20rem;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 360px) {
  .header p {
    font-size: 4rem;
    margin-top: 5rem;
  }
}
.header .arrow {
  bottom: -70px;
  display: block;
  height: 100px;
  margin: 0 auto;
  position: relative;
}
@media (min-width: 360px) {
  .header .arrow {
    bottom: 0px;
  }
}

.header__content {
  align-items: center;
  color: white;
  display: flex;
  font-family: "Lato", sans-serif;
  font-weight: 900;
  justify-content: space-between;
  margin: 0 auto;
  padding-top: 2rem;
}
@media (min-width: 360px) {
  .header__content {
    width: 85%;
  }
}
@media (min-width: 768px) {
  .header__content {
    width: 95%;
  }
}

.header__nav {
  display: flex;
  justify-content: space-between;
}
.header__nav a {
  color: white;
  padding: 1.2rem 2.2rem;
  text-decoration: none;
}
.header__nav a:last-child {
  background-color: white;
  border-radius: 20px;
  color: black;
  font-family: "Fraunces", serif;
  font-weight: 700;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .header__nav {
    width: 500px;
  }
}

@media (min-width: 360px) {
  .header__nav--desktop {
    display: none;
  }
}
@media (min-width: 768px) {
  .header__nav--desktop {
    display: block;
  }
}

.header__nav--mobile img:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .header__nav--mobile {
    display: none;
  }
}